export default {
  name: "BaseTextField",
  props: {
    title: {
      type: String,
      default: ""
    },
    type: {
      type: String,
      default: "text"
    },
    value: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      default: ""
    },
    clearable: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    inputHandle(event) {
      this.$emit("input", event.target.value)
    },
    clear() {
      this.$emit("input", "")
    }
  }
}
